<template>
  <div class="home">
    <MglMap
      class="map"
      :accessToken="accessToken"
      :mapStyle="mapStyle"
      :center="center"
      :zoom.sync="zoom"
      @load="onMapLoaded"
    >
      <MglNavigationControl position="top-right"/>
    </MglMap>
    <div v-if="mapLoaded">
      <MarkerDonut
        v-if="zoom >=2 && zoom <= 4 && rails.october.counts.length > 0 && rails.october.loadPercent"
        :counts="rails.october.counts"
        :colors="donutColors"
        :number="rails.october.loadPercent"
        :lngLat="rails.october.lngLat"
      />
    </div>
    <v-card
      v-if="panelStation"
      class="overlay"
      width="374"
      rounded
      elevation="3"
    >
      <v-card-title>
        <span>
          {{ station.name }}
        </span>
        <v-spacer></v-spacer>
        <v-btn @click="panelStation = false" icon>
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-card-subtitle>
        Октябрьская ж. д., {{station.region}} регион
        <v-divider/>
      </v-card-subtitle>

      <v-list
      >
        <v-list-item
          v-for="(way, i) in station.ways.public"
          :key="i"
          two-line
        >
          <v-list-item-content>
            <v-list-item-title>Путь {{ way.name }}</v-list-item-title>
            <v-list-item-subtitle> Условная длина {{ way.length }} </v-list-item-subtitle>
          </v-list-item-content>

          <v-list-item-action>
            <v-checkbox
              v-model="way.state"
              color="red"
            ></v-checkbox>
          </v-list-item-action>
        </v-list-item>
      </v-list>
    </v-card>
    <LegendRails v-if="zoom >=2 && zoom <= 4"/>
  </div>
</template>

<script>
// @ is an alias to /src
import Mapbox from 'mapbox-gl'
import { MglMap, MglNavigationControl } from 'vue-mapbox'
import octoberRailway from '@/assets/octoberRail.json'
import mapStyle from '@/assets/map-style.json'
import LegendRails from '@/components/LegendRails'
import MarkerDonut from '@/components/MarkerDonut'
import styleVariables from '@/css/variables.sass'

export default {
  name: 'Home',
  components: {
    LegendRails,
    MarkerDonut,
    MglMap,
    MglNavigationControl
  },

  data: () => ({
    accessToken: 'pk.eyJ1IjoiY2hpZ2EiLCJhIjoiY2tqYnRyN2NzN3B4ZTJxcWpvdmVldGdmYSJ9.mTIH5Hby4yDyU-LLFOY0Fw',
    mapStyle: mapStyle,
    zoom: 3,
    mapLoaded: false,
    center: [83.214, 60.703],
    donutColors: [
      styleVariables.donutRed,
      styleVariables.donutLightRed,
      styleVariables.donutGrey,
      styleVariables.donutLightGrey
    ],
    donutCounts: [1, 1, 1, 1],
    rails: {
      moscow: {
        lngLat: [36.724702, 53.889617],
        counts: [],
        loadPercent: null
      },
      october: {
        lngLat: [34.361234, 62.771296],
        counts: [],
        loadPercent: null
      },
      east_siberian: {
        lngLat: [107.392017, 57.139482],
        counts: [],
        loadPercent: null
      },
      north_caucasus: {
        lngLat: [42.598601, 45.707442],
        counts: [],
        loadPercent: null
      },
      far_eastern: {
        lngLat: [132.20322, 49.712039],
        counts: [],
        loadPercent: null
      },
      zabaikal: {
        lngLat: [119.10244, 53.795598],
        counts: [],
        loadPercent: null
      },
      privolzhsk: {
        lngLat: [46.002965, 49.115728],
        counts: [],
        loadPercent: null
      },
      gorky: {
        lngLat: [49.439791, 57.691804],
        counts: [],
        loadPercent: null
      },
      west_siberian: {
        lngLat: [80.111098, 55.050281],
        counts: [],
        loadPercent: null
      },
      kaliningrad: {
        lngLat: [21.262665, 54.806304],
        counts: [],
        loadPercent: null
      },
      krasnoyarsk: {
        lngLat: [93.449533, 55.668329],
        counts: [],
        loadPercent: null
      },
      kuybyshev: {
        lngLat: [49.74429, 54.232178],
        counts: [],
        loadPercent: null
      },
      sverdlovsk: {
        lngLat: [68.875671, 62.062656],
        counts: [],
        loadPercent: null
      },
      northern: {
        lngLat: [54.752627, 64.463401],
        counts: [],
        loadPercent: null
      },
      south_eastern: {
        lngLat: [40.218175, 51.563952],
        counts: [],
        loadPercent: null
      },
      south_urals: {
        lngLat: [59.424326, 53.67003],
        counts: [],
        loadPercent: null
      },
      crimea: {
        lngLat: [34.559016, 45.295494],
        counts: [],
        loadPercent: null
      }
    },
    popups: {},
    octoberRailway: octoberRailway,
    panelStation: false,
    station: {
      name: null,
      lngLat: [],
      esr: null,
      ways: {
        public: [{
          name: null,
          length: null
        }],
        private: [{
          name: null,
          length: null
        }]
      },
      region: null
    }
  }),

  methods: {
    onMapLoaded: function (event) {
      this.$store.map = event.map
      this.mapLoaded = true

      this.createRailAreas()
      this.createStations()
      this.updateDonutChartData()

      const map = this.$store.map
      // click and hover on station
      map.on('click', 'station-point', (event) => {
        this.openPanelStation(event.features[0].properties.esr)
      })
      map.on('mouseenter', 'station-point', function () {
        map.getCanvas().style.cursor = 'pointer'
      })
      map.on('mouseleave', 'station-point', function () {
        map.getCanvas().style.cursor = ''
      })
      // popups for station
      map.on('moveend', () => {
        if (this.zoom > 11) {
          const features = map.queryRenderedFeatures({ layers: ['station-point'] })
          // let updatedPopups = []
          for (const feature of features) {
            const id = feature.id
            const coordinates = feature.geometry.coordinates
            if (!Object.prototype.hasOwnProperty.call(this.popups, id)) {
              this.popups[id] = new Mapbox.Popup({
                closeButton: false,
                closeOnClick: false
              })
                .setLngLat(coordinates)
                .setHTML(
                  `<span class="font-weight-bold">${feature.properties.name}</span>
                  <br>
                  о - ${feature.properties.busy}/${feature.properties.total}
                  <br>
                  н - нет данных
                  `
                )
                .addTo(map)
            } else {
              this.popups[id].setLngLat(coordinates)
            }
          }
        } else {
          for (const id in this.popups) {
            const popup = this.popups[id]
            popup.remove()
            delete this.popups[id]
          }
        }
      })
    },
    createRailAreas () {
      this.$store.map.addSource('rail-areas', {
        type: 'geojson',
        data: require('@/assets/railAreas.json')
      })
      this.$store.map.addLayer({
        id: 'rail-polygons',
        type: 'fill',
        source: 'rail-areas',
        layout: {},
        maxzoom: 6,
        paint: {
          'fill-color': [
            'match', ['get', 'code'],
            [10, 24, 61, 83, 96], styleVariables.railArea1,
            [28, 41, 63, 88], styleVariables.railArea2,
            [17, 51, 80, 92], styleVariables.railArea3,
            [1, 58, 76, 94], styleVariables.railArea4,
            /* other */ 'grey'
          ],
          'fill-opacity': [
            'interpolate', ['linear'], ['zoom'],
            0, 0.7,
            2, 0.20,
            5, 0.20,
            6, 0
          ]
        }
      })
      this.$store.map.addLayer({
        id: 'rail-borders',
        type: 'line',
        source: 'rail-areas',
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        minzoom: 5.5,
        paint: {
          'line-color': styleVariables.railBorder,
          'line-width': 2,
          'line-opacity': [
            'interpolate', ['linear'], ['zoom'],
            5.5, 0,
            6.5, 0.6
          ]
        }
      })
    },
    createStations () {
      this.$store.map.addSource('stations', {
        type: 'geojson',
        data: this.getStationsGeoJSON()
      })

      this.$store.map.addLayer({
        id: 'station-point',
        type: 'circle',
        source: 'stations',
        minzoom: 3.8,
        paint: {
          'circle-stroke-width': 1,
          'circle-stroke-color': 'white',
          'circle-stroke-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            3.8, 0,
            4, 1
          ],
          'circle-radius': {
            stops: [
              [3, 2.5],
              [6, 5],
              [12, 7]
            ]
          },
          'circle-color': [
            'interpolate',
            ['linear'],
            ['get', 'load'],
            0, 'green',
            0.5, 'yellow',
            1, 'red'
          ],
          'circle-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            3.8, 0,
            4, 1
          ]
        }
      })
    },
    getStationsGeoJSON () {
      const stationsFeatures = this.octoberRailway.stations.map(station => {
        const total = station.vid_udv
        const busy = (station.svb_udv === 0 && station.zan_udv === 0) ? 0 : station.zan_udv
        const free = (station.svb_udv === 0 && station.zan_udv === 0) ? station.vid_udv : station.svb_udv
        const load = station.zap_udv_percent / 100
        station.load = load
        return {
          type: 'Feature',
          id: station.esr,
          properties: {
            load,
            name: station.name,
            esr: station.esr,
            total,
            busy,
            free
          },
          geometry: {
            type: 'Point',
            coordinates: station.lngLat
          }
        }
      })

      return {
        type: 'FeatureCollection',
        features: stationsFeatures
      }
    },
    updateDonutChartData () {
      const { total, free } = this.octoberRailway.stations.reduce((accumulator, station) => {
        const total = station.vid_udv
        const free = (station.svb_udv === 0 && station.zan_udv === 0) ? station.vid_udv : station.svb_udv > 0 ? station.svb_udv : 0
        return {
          total: accumulator.total + total,
          free: accumulator.free + free
        }
      }, { total: 0, free: 0 })
      this.rails.october.counts = [total - free, free, 0, 0]
      this.rails.october.loadPercent = Math.round((total - free) / total * 100)
    },
    openPanelStation (esr) {
      const index = this.octoberRailway.stations.findIndex(station => {
        return station.esr === esr
      })
      this.station = this.octoberRailway.stations[index]
      this.panelStation = true
    }
  },

  watch: {
    octoberRailway: {
      handler: function () {
        this.$store.map.getSource('stations').setData(this.getStationsGeoJSON())
        this.updateDonutChartData()
      },
      deep: true
    }
  }
}
</script>

<style scoped lang="sass">
.map
  position: absolute
  top: 0
  bottom: 0
  width: 100%

.overlay
  position: absolute
  user-select: none
  margin: 10px
</style>
